<template>
  <div>
    <NavTemplent title="采购费用"></NavTemplent>
    <div style="padding: 15px 10% 15px 15px;">
      <div>
        <h5 style="font-weight: bold">数据筛选</h5>
        <el-form style="margin-top: 15px" label-width="80px" inline @submit.native.prevent size="mini">
<!--          <el-date-picker-->
<!--            @change="updateSite"-->
<!--            style="width: 250px"-->
<!--            size="mini"-->
<!--            v-model="form.date"-->
<!--            type="daterange"-->
<!--            format="yyyy/MM/dd"-->
<!--            start-placeholder="开始日期"-->
<!--            end-placeholder="结束日期">-->
<!--          </el-date-picker>-->
          <el-form-item label="站点">
            <el-select v-model="form.site" placeholder="请选择站点" @change="updateSite">
              <el-option v-for="item in siteList" :key="item.web_id" :value="item.web_id" :label="item.web_name"></el-option>
            </el-select>
          </el-form-item>
<!--          <el-form-item>-->
<!--            <el-button type="primary" @click="getOrderCost">-->
<!--              <i class="el-icon-search"></i>-->
<!--              筛选</el-button>-->
<!--          </el-form-item>-->
        </el-form>
      </div>
      <el-row>
        <el-col :span="24">
          <div id="myChart" style="width: 100%;height: 400px"></div>
        </el-col>
      </el-row>
      <div class="tableTitle">
        <span style="width: 50%;float: left">
          <div style="float: left;width: 100px">采购列表</div>
          <el-form style="float: left" label-width="80px" inline @submit.native.prevent size="mini">
            <el-date-picker
              @change="getOrderCost"
              style="width: 250px"
              size="mini"
              v-model="form.date"
              type="daterange"
              format="yyyy/MM/dd"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker>
          </el-form>
        </span>
        <span style="width: 50%;float: right;text-align: right">
          <el-button size="mini" type="primary" @click="downLoad"><i class="el-icon-download"></i>导出</el-button>
        </span>
      </div>
      <el-table
        :data="orderCostList"
        border
        style="width: 100%">
        <el-table-column
          type="index">
        </el-table-column>
        <el-table-column
          label="仓库SKU" width="180px">
          <template slot-scope="scope">
            <span>{{scope.row.productBarcode || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="采购负责人">
          <template slot-scope="scope">
            <span>{{scope.row.buyerName || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="采购数量">
          <template slot-scope="scope">
            <span>{{scope.row.quantity || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="采购价">
          <template slot-scope="scope">
            <span>{{scope.row.avgPurchasePrice || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="平均单价">
          <template slot-scope="scope">
            <span>{{scope.row.avgUnitPriceOrg || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="总成本">
          <template slot-scope="scope">
            <span>{{scope.row.totalCost || 0}}</span>
          </template>
        </el-table-column>
<!--        <el-table-column-->
<!--          label="发货日期">-->
<!--          <template slot-scope="scope">-->
<!--            <span>{{ scope.row.soShipTime | dateFormat('YYYY/MM/DD') || '&#45;&#45;'}}</span>-->
<!--          </template>-->
<!--        </el-table-column>-->
        <el-table-column
          label="采购日期">
          <template slot-scope="scope">
            <span>{{ scope.row.pay_time | dateFormat('YYYY/MM/DD')}}</span>
          </template>
        </el-table-column>
      </el-table>
      <TablePage :pageSize="page.pageNum" :pageNum="page.pageSize" :total="total" @update:pageSize="updateSize" @update:pageNum="updateNum"></TablePage>
    </div>
  </div>
</template>
<script>
    import moment from 'moment';
    import NavTemplent from "../../../components/NavTemplent";
    import TablePage from "../../../components/TablePage";
    export default {
        components: {
            NavTemplent,
            TablePage
        },
        data() {
            return {
                orderCostList: [],
                isDownload: '0',
                page: {
                    pageSize: 1,
                    pageNum: 50
                },
                total: 0,
                siteList: [],
                orderCostChartList: [],
                orderCostChartListTime: [],
                form: {
                    site: '',
                    date: [moment(moment().valueOf() - 365 * 24 * 3600 * 1000).format('YYYY/MM/DD'), moment(moment() + 24 * 3600 * 1000).format('YYYY/MM/DD')],
                }
            }
        },
        methods: {
            // 站点列表
            getSiteList() {
                this.$post('/getGaWebSitList', {
                    pageNum: 10000,
                    page: this.page.pageSize,
                    website_name: this.form.siteName
                }).then(res => {
                    this.siteList = res.list
                })
            },
            updateSite() {
                this.getOrderCost();
                this.getOrderCostChart()
            },
            updateSize(val) {
                this.page.pageSize = 1;
                this.page.pageNum = val;
                this.getOrderCost();
            },
            updateNum(val) {
                this.page.pageSize = val;
                this.getOrderCost();
            },
            getOrderCost() {
                this.startTime = this.$root.$options.filters.dateFormat(this.form.date[0], 'YYYY/MM/DD');
                this.lastTime = this.$root.$options.filters.dateFormat(this.form.date[1], 'YYYY/MM/DD');
                this.$post('/getOrderCostList', {
                    page: this.page.pageSize,
                    pageNum: this.page.pageNum,
                    start_time: this.startTime,
                    end_time: this.lastTime,
                    web_id: this.form.site,
                    download: this.isDownload
                }).then(res => {
                    this.orderCostList = res.list;
                    this.total = res.count
                })
            },
            downLoad() {
                this.isDownload = 1;
                this.startTime = this.$root.$options.filters.dateFormat(this.form.date[0], 'YYYY/MM/DD');
                this.lastTime = this.$root.$options.filters.dateFormat(this.form.date[1], 'YYYY/MM/DD');
                this.$exportExcel('/getOrderCostList', {
                    pageNum: this.page.pageNum,
                    page: this.page.pageSize,
                    start_time: this.startTime,
                    end_time: this.lastTime,
                    web_id: this.form.site,
                    download: this.isDownload
                }).then(() => {
                    this.$message.success('下载成功')
                })
            },
            getOrderCostChart() {
                this.$post('getOrderCostChart', {
                    web_id: this.form.site
                }).then(res => {
                    this.orderCostChartListTime = res.order_cost.map(item => {
                        return item.date
                    });
                    this.orderCostChartList = res.order_cost.map(item => {
                        return item.total_price
                    });
                    if (res.order_cost.length) {
                        this.drawLine()
                    }
                })
            },
            drawLine() {
                // 基于准备好的dom，初始化echarts实例
                let myChart = this.$echarts.init(document.getElementById('myChart'));
                // 绘制图表
                myChart.setOption({
                    title: {text: '采购费用'},
                    xAxis: {
                        type: 'category',
                        boundaryGap: false,
                        data: this.orderCostChartListTime
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: this.orderCostChartList,
                        type: 'line',
                        areaStyle: {}
                    }]
                });
            }
        },
        mounted() {
            this.getOrderCostChart();
            this.getSiteList();
            this.getOrderCost();
        }
    }
</script>

<style lang="scss" scoped>
  .tableTitle{
    height: 60px;
    line-height: 60px;
    padding: 0 15px;
    background: #eee;
    margin-bottom: 15px;
    div{
      font-weight: bolder;
    }
  }
  .el-select, .date{
    width: 100%;
  }
</style>



